(function() {
  'use strict';

  angular
    .module('sherFrontend')
    .factory('AuthenticationService', AuthenticationService)
    .factory('Base64', Base64);

    /** @ngInject */
    function AuthenticationService(Base64, $http, $cookieStore, $rootScope, $timeout) {
        var service = {};

        service.Login = function (username, password, callback) {

            /* Dummy authentication for testing, uses $timeout to simulate api call
             ----------------------------------------------*/
            $timeout(function () {
                var response = { success: username === 'test' && password === 'test' };
                if (!response.success) {
                    response.message = 'Username or password is incorrect';
                }
                callback(response);
            }, 1000);


            /* Use this for real authentication
             ----------------------------------------------*/
            //$http.post('/api/authenticate', { username: username, password: password })
            //    .success(function (response) {
            //        callback(response);
            //    });

        };

        service.SetCredentials = function (username, password) {
            var authdata = Base64.encode(username + ':' + password);

            $rootScope.globals = {
                currentUser: {
                    username: username,
                    authdata: authdata
                }
            };

         /*   $http.defaults.headers.common['Authorization'] = 'Basic ' + authdata; */
            $cookieStore.put('globals', $rootScope.globals);
        };

        service.ClearCredentials = function () {
            $rootScope.globals = {};
            $cookieStore.remove('globals');
        /*    $http.defaults.headers.common.Authorization = 'Basic ';*/
        };

        return service;
    }

    function Base64() {
    var keyStr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=';

    return {
        encode: function (input) {
            var output = "";
            var chr1, chr2, chr3 = "";
            var enc1, enc2, enc3, enc4 = "";
            var i = 0;

            do {
                chr1 = input.charCodeAt(i++);
                chr2 = input.charCodeAt(i++);
                chr3 = input.charCodeAt(i++);

                enc1 = chr1 >> 2;
                enc2 = ((chr1 & 3) << 4) | (chr2 >> 4);
                enc3 = ((chr2 & 15) << 2) | (chr3 >> 6);
                enc4 = chr3 & 63;

                if (isNaN(chr2)) {
                    enc3 = enc4 = 64;
                } else if (isNaN(chr3)) {
                    enc4 = 64;
                }

                output = output +
                    keyStr.charAt(enc1) +
                    keyStr.charAt(enc2) +
                    keyStr.charAt(enc3) +
                    keyStr.charAt(enc4);
                chr1 = chr2 = chr3 = "";
                enc1 = enc2 = enc3 = enc4 = "";
            } while (i < input.length);

            return output;
        },

        decode: function (input) {
            var output = "";
            var chr1, chr2, chr3 = "";
            var enc1, enc2, enc3, enc4 = "";
            var i = 0;

            // remove all characters that are not A-Z, a-z, 0-9, +, /, or =
            var base64test = /[^A-Za-z0-9\+\/\=]/g;
            if (base64test.exec(input)) {
                window.alert("There were invalid base64 characters in the input text.\n" +
                    "Valid base64 characters are A-Z, a-z, 0-9, '+', '/',and '='\n" +
                    "Expect errors in decoding.");
            }
            input = input.replace(/[^A-Za-z0-9\+\/\=]/g, "");

            do {
                enc1 = keyStr.indexOf(input.charAt(i++));
                enc2 = keyStr.indexOf(input.charAt(i++));
                enc3 = keyStr.indexOf(input.charAt(i++));
                enc4 = keyStr.indexOf(input.charAt(i++));

                chr1 = (enc1 << 2) | (enc2 >> 4);
                chr2 = ((enc2 & 15) << 4) | (enc3 >> 2);
                chr3 = ((enc3 & 3) << 6) | enc4;

                output = output + String.fromCharCode(chr1);

                if (enc3 != 64) {
                    output = output + String.fromCharCode(chr2);
                }
                if (enc4 != 64) {
                    output = output + String.fromCharCode(chr3);
                }

                chr1 = chr2 = chr3 = "";
                enc1 = enc2 = enc3 = enc4 = "";

            } while (i < input.length);

            return output;
        }
        };        
    }
})();

(function() {
  'use strict';

  angular
    .module('sherFrontend')
    .factory('jobManager', jobManager);

  /** @ngInject */
  function jobManager($log, $resource, $http, sherConfig) {
    var api = sherConfig.API;

    var jobs = [];
    var healthyCount = 0;
    var unhealthCount = 0;
    var resource = $resource(api + '/jobs', {}, {
        query: {
            method: 'get',
            timeout: 20000
        },
    })

    function getJobs (callback) {
      return resource.query({

      }, function(r) {
        return callback && callback(r);
      })
    };

    function checkFormat(job) {
        for(var i = 0; i < job.tasks.length; i++) {
            job.tasks[i].scale = parseInt(job.tasks[i].scale);
            job.tasks[i].cpus = parseFloat(job.tasks[i].cpus);
            job.tasks[i].mem = parseFloat(job.tasks[i].mem);
            job.tasks[i].disk = parseFloat(job.tasks[i].disk);
        }
        return job;
    }
    function handleJobs(jobs) {
        jobs.sort(function(a, b) {
            return b.create_time - a.create_time;
        })

        return jobs
    } 

    function emptyArray(size) {
        var data = new Array();
        for(var i = 0; i < size; i++) {
            data.push(0);
        }
        return data;
    }

    return {
      // 刷新任务
      refresh: function() {
        return getJobs(function(response) {
          jobs = handleJobs(response.message);
        })
      },

      // 重置数据
      resetData: function() {
        jobs = [];
      },

      // 获取全部的任务
      getAllJobs: function() {
        return jobs;
      },

      // 搜索任务
      getJobs: function(key) {
        if(key == 'all') {
          return jobs;
        } else {
          var result = [];
          var pattern = new RegExp(key,'ig');
          for (var i = 0; i < jobs.length; i++) {
            if(JSON.stringify(jobs[i]).match(pattern)) {
              result.push(jobs[i]);
            }
          }
          return result;
        }
      },

      // 按状态过滤任务
      getJobsByStatus: function(status) {
        if(status == 'all') {
          return jobs;
        } else {
          var result = [];
          for (var i = 0; i < jobs.length; i++) {
            if (jobs[i].status.toLowerCase() == status.toLowerCase()) {
              result.push(jobs[i]);
            }
          }
          return result;
        }
      },   

      // 按任务健康状态过滤任务
      getJobsByHealth: function(health) {
        if(health == 'all') {
            return jobs;
          } else {
            var result = [];
            for (var i = 0; i < jobs.length; i++) {
              if (jobs[i].health.toLowerCase() == health.toLowerCase()) {
                result.push(jobs[i]);
              }
            }
            return result;
          }
      },

      // 按ID获取任务
      getById: function(id) {
        if (!!jobs) {
          for (var i = 0; i < jobs.length; i++) {
            if (jobs[i].id === id) {
              return jobs[i];
            }
          }
        } else {
          return null;
        }
      },

      // 提交任务
      submitJob: function(job, callback, errorHandle) {
        job = checkFormat(job);
        $http({
          method: 'POST',
          url: api + '/jobs',
          data : job,
          headers:{
            'Accept': 'application/json',
            'Content-Type': 'application/json; ; charset=UTF-8'
          }
        }).success(function(response) {
          return callback && callback(response);
        }).error(function(response) {
          return errorHandle && errorHandle(response);
        });
      },

      // 删除任务
      deleteJob: function(id, callback, errorHandle) {
        $http({
          method: 'DELETE',
          url: api + '/jobs/' + id
        }).success(function(response) {
            return callback && callback(response);
        }).error(function(response) {
            return errorHandle && errorHandle(response);
        });
      },
    }
  }
})();

